<template>
  <div class="moveInOut" @click="closeByDialog">
    <AppHeader>
      <p slot="center">移入移出明细</p>
    </AppHeader>

    <div style="height: 100px;">
      <div class="search-header-cont">
        <div class="search-header bg-fff">
          <van-search
            placeholder="请输入关键词搜索"
            shape="round"
            v-model="searchKey"
            @search="confirmSearch"
          />
        </div>
      </div>

      <div class="date-filters-cont">
        <div class="date-filters">
          <input
            type="text"
            @click.stop="openByDialog"
            :value="calendar.display"
            readonly
          />
        </div>
        <transition name="fade">
          <div class="calendar-dialog" v-if="calendar.show">
            <div class="calendar-dialog-mask" @click="closeByDialog"></div>
            <div class="calendar-dialog-body">
              <calendar
                :range="calendar.range"
                :zero="calendar.zero"
                :lunar="calendar.lunar"
                :value="calendar.value"
                @select="calendar.select"
              ></calendar>
            </div>
          </div>
        </transition>
      </div>
    </div>

    <div class="detailed-table" v-if="hasData">
      <div class="table-content">
        <table>
          <thead>
            <tr>
              <th>序号</th>
              <th>部门编号</th>
              <th>商品ID</th>
              <th>商品名称</th>
              <th>第二代码</th>
              <th>小类Code</th>
              <th>小类名称</th>
              <th>核算售价</th>
              <th>合同进价</th>
              <th>商品创建日期</th>
              <th>最后配货时间</th>
              <th>品牌</th>
              <th>供应商ID</th>
              <th>供应商</th>
              <th>库存数</th>
              <th>库存成本</th>
              <th>类型</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(items, index) in resultData" :key="index">
              <td>{{ currentPage * 10 - 10 + index + 1 }}</td>
              <td>{{ items.code }}</td>
              <td>{{ items.gcode }}</td>
              <td>{{ items.gname }}</td>
              <td>{{ items.gcode2 }}</td>
              <td>{{ items.gsort }}</td>
              <td>{{ items.sname }}</td>
              <td>{{ items.gRtlprc }}</td>
              <td>{{ items.gCntinprc }}</td>
              <td>{{ items.gCreatedate }}</td>
              <td>{{ items.iAdate }}</td>
              <td>{{ items.bname }}</td>
              <td>{{ items.vgid }}</td>
              <td>{{ items.vname }}</td>
              <td>{{ items.stockNum }}</td>
              <td>{{ items.stockAmount }}</td>
              <td>{{ items.type }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="pagination-module" v-if="hasData">
      <el-pagination
        background
        hide-on-single-page
        layout="prev, pager, next, jumper"
        :pager-count="5"
        :page-size="10"
        :total="1000"
        :current-page="currentPage"
        @current-change="pageCurrentChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import {
  Search,
  Cell,
  CellGroup,
  Icon,
  Field,
  DatetimePicker,
  Button,
  Popup
} from "vant";

import ElementUI from "element-ui";

Vue.use(Search)
  .use(Cell)
  .use(CellGroup)
  .use(Field)
  .use(DatetimePicker)
  .use(Popup)
  .use(Button)
  .use(Icon);
Vue.use(ElementUI);

// @ is an alias to /src
import AppHeader from "@/components/AppHeader.vue";
import calendar from "@/components/calendar/calendar.vue";
import "element-ui/lib/theme-chalk/index.css";

export default {
  name: "Search",
  components: {
    AppHeader,
    calendar
  },

  data() {
    return {
      searchKey: "",
      calendar: {
        display: "2020/02/16", //输入框站位日期
        show: false, //是否显示
        // range: true,
        zero: true,
        value: [2019, 2, 16], //默认选中日期
        lunar: true, //显示农历
        select: value => {
          // console.log(value);
          this.calendar_startDate = value.join("-");
          this.calendar.show = false;
          this.calendar.value = value;
          this.calendar.display = value.join("-");
          this.$nextTick(() => {
            this.get_data();
          });
        }
      },
      calendar_startDate: "",
      calendar_endDate: "",
      hasData: false,
      resultData: [],
      currentPage: 1,
      recordcount: "",
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.get_defaultData();
      this.get_data();
    });
  },
  methods: {
    confirmSearch(val) {
      this.searchKey = val;
      this.get_data();
    },
    openByDialog() {
      this.calendar.show = true;
    },
    closeByDialog() {
      this.calendar.show = false;
    },
    cancelFn() {
      this.datetime_picker_show = false;
    },
    pageCurrentChange(val) {
      this.currentPage = val;
      this.get_data();
    },
    get_defaultData() {
      this.getStoreInfo = this.$store.getters.getStoreInfo; //获取当前选择的门店信息
      // console.log(this.$store.state.defaultDate);
      const defaultTime = this.formatDate.pastDate(2, "-");
      const currentTime = this.formatDate.pastDate(0, "-");
      this.calendar_startDate = defaultTime; //日历默认开始时间
      this.calendar_endDate = currentTime; //日历默认结束时间
      this.calendar.value = defaultTime.split("-"); //日历默认选中日期
      //日历-输入框站位日期
      this.calendar.display = this.formatDate.pastDate(2, "-");
    },
    // 获取数据
    get_data() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.getStoreInfo = this.$store.getters.getStoreInfo;
      let params = {
        storeId: this.getStoreInfo.gid,
        startDate: this.calendar_startDate,
        searchKey: this.searchKey,
        pageSize: 10,
        pageNo: this.currentPage
      };
      this.serverHost
        .fetchPost("/dailyMoveInfo", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          if (resultData.length == false) {
            this.hasData = false;
          } else {
            this.hasData = true;
            this.resultData = resultData;
            this.recordcount = res.data.count;
          }
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    }
  }
};
</script>

